    text-align: right;
}

.section-5 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 50vh;
    width: 80%;
    grid-gap: 10px;
    gap: 10px;
}

.section-5 h1 {
    font-size: 30px;
    font-family: 'Darker Grotesque', sans-serif;
    font-weight: 400;
}

.link-tag {
